/**
 * ÈÇÆ‰ª∂ÊúçÂä°Ê®°Âùó
 * ÊîØÊåÅ SendGrid Âíå Resend ÈÇÆ‰ª∂ÊúçÂä°
 */

import { Resend } from 'resend';
import sgMail from '@sendgrid/mail';

// ÁéØÂ¢ÉÂèòÈáè
const SENDGRID_API_KEY = process.env.SENDGRID_API_KEY;
const RESEND_API_KEY = process.env.RESEND_API_KEY;
const FROM_EMAIL = process.env.SENDGRID_FROM_EMAIL || 'noreply@sociomint.com';
const EMAIL_PROVIDER = process.env.EMAIL_PROVIDER || 'resend'; // 'sendgrid' | 'resend'

// ÂàùÂßãÂåñÈÇÆ‰ª∂ÊúçÂä°
let resend: Resend | null = null;
let sendgridInitialized = false;

if (EMAIL_PROVIDER === 'resend' && RESEND_API_KEY) {
  resend = new Resend(RESEND_API_KEY);
  console.log('‚úÖ Resend email service initialized');
} else if (EMAIL_PROVIDER === 'sendgrid' && SENDGRID_API_KEY) {
  sgMail.setApiKey(SENDGRID_API_KEY);
  sendgridInitialized = true;
  console.log('‚úÖ SendGrid email service initialized');
} else {
  console.warn('‚ö†Ô∏è No email service configured');
}

// ÈÇÆ‰ª∂Ê®°ÊùøÁ±ªÂûã
export interface EmailTemplate {
  subject: string;
  html: string;
  text?: string;
}

// ÈÇÆ‰ª∂ÂèëÈÄÅÈÄâÈ°π
export interface EmailOptions {
  to: string | string[];
  subject: string;
  html: string;
  text?: string;
  from?: string;
  replyTo?: string;
  attachments?: Array<{
    filename: string;
    content: string | Buffer;
    type?: string;
  }>;
}

/**
 * ÂèëÈÄÅÈÇÆ‰ª∂ÔºàÈÄöÁî®ÂáΩÊï∞Ôºâ
 */
export async function sendEmail(options: EmailOptions): Promise<{
  success: boolean;
  messageId?: string;
  error?: string;
}> {
  try {
    if (EMAIL_PROVIDER === 'resend' && resend) {
      return await sendWithResend(options);
    } else if (EMAIL_PROVIDER === 'sendgrid' && sendgridInitialized) {
      return await sendWithSendGrid(options);
    } else {
      throw new Error('No email service configured');
    }
  } catch (error: any) {
    console.error('‚ùå Email sending failed:', error);
    return {
      success: false,
      error: error.message
    };
  }
}

/**
 * ‰ΩøÁî® Resend ÂèëÈÄÅÈÇÆ‰ª∂
 */
async function sendWithResend(options: EmailOptions): Promise<{
  success: boolean;
  messageId?: string;
  error?: string;
}> {
  if (!resend) {
    throw new Error('Resend not initialized');
  }

  const { data, error } = await resend.emails.send({
    from: options.from || FROM_EMAIL,
    to: Array.isArray(options.to) ? options.to : [options.to],
    subject: options.subject,
    html: options.html,
    text: options.text,
    reply_to: options.replyTo,
    attachments: options.attachments?.map(att => ({
      filename: att.filename,
      content: att.content
    }))
  });

  if (error) {
    throw new Error(error.message);
  }

  return {
    success: true,
    messageId: data?.id
  };
}

/**
 * ‰ΩøÁî® SendGrid ÂèëÈÄÅÈÇÆ‰ª∂
 */
async function sendWithSendGrid(options: EmailOptions): Promise<{
  success: boolean;
  messageId?: string;
  error?: string;
}> {
  const msg = {
    to: options.to,
    from: options.from || FROM_EMAIL,
    subject: options.subject,
    html: options.html,
    text: options.text,
    replyTo: options.replyTo,
    attachments: options.attachments?.map(att => ({
      filename: att.filename,
      content: Buffer.isBuffer(att.content) ? att.content.toString('base64') : att.content,
      type: att.type || 'application/octet-stream',
      disposition: 'attachment'
    }))
  };

  const [response] = await sgMail.send(msg);

  return {
    success: true,
    messageId: response.headers['x-message-id']
  };
}

/**
 * ÂèëÈÄÅÊ¨¢ËøéÈÇÆ‰ª∂
 */
